<template>
  <div>
    <h2
      v-if="$mq !== 'sm'"
      class="menu-title"
    >
      Selecteer objecten
    </h2>
    <div class="menu-item">
      <h3 class="menu-item__title">
        {{ item.title }}
      </h3>
      <ul class="menu-item__products">
        <li
          v-for="(product, index) in item.products"
          :key="index"
        >
          {{ product.name }}
          <input
            type="number"
            v-model="product.qty"
            @change="setQty"
          >
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'cn-menu-item',
  props: {
    item: {
      type: Object,
      default () {
        return {}
      }
    },
    title: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      total: 0
    }
  },
  watch: {
    items: 'setQty'
  },
  methods: {
    setQty () {
      this.total = 0
      this.item.products.forEach(product => {
        this.total += (parseInt(product.qty) * product.attributes.m3)
      })

      this.$set(this.item, 'qty', this.total)
    }
  }
}
</script>

